import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import Image from "gatsby-image";

import SocialIcons from "./social-icons";

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/profile-pic.webp/" }) {
        childImageSharp {
          fixed(width: 100, height: 100, quality: 95) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
            summary
          }
        }
      }
    }
  `);

  // Set these values by editing "siteMetadata" in gatsby-config.js
  const author = data.site.siteMetadata?.author;
  const avatar = data?.avatar?.childImageSharp?.fixed;

  return (
    <div className="bio">
      <div>
        <div className="bio__wrapper">
          <div className="bio__meta">
            {avatar && (
              <Image
                fixed={avatar}
                alt={author?.name || ``}
                className="bio__avatar"
              />
            )}
            {author?.name && <strong>{author.name}</strong>}
          </div>
          <SocialIcons direction="row" />
        </div>
        <div className="bio__summary">{author?.summary || null}</div>
      </div>
    </div>
  );
};

export default Bio;
